@import "src/common/styles/sass/variables";

.workItem {
    margin-bottom: 40px;
    .picture {
        width:600px;
        height: 300px;
        background-color: cornflowerblue;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size: cover;
        background-position: top;
        background-repeat: no-repeat;
        position: relative;
        border-radius: 10px;
        transition: 0.5s;

        &:hover {
            transform: scale(1.1,1.1)
        }
        @media screen and (max-width: 757px) {
            width:400px;
            height: 200px;
        }
        @media screen and (max-width: 500px) {
            width:300px;
            height: 200px;
        }
        @media screen and (max-width: 380px) {
            width:200px;
            height: 300px;
        }
    }
    .worksDescription{
        width: 600px;
        background-color: #002245;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0.8;
        color: #f9f9f9;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;

        a {
            color: $textColor;
            text-decoration: none;
            &:hover {
                color: $spanColor;
                font-weight: bold;
            }
        }
        @media screen and (max-width: 757px) {
            width:400px;
            height: 200px;
        }
        @media screen and (max-width: 500px) {
            width:300px;
            height: 200px;
        }
        @media screen and (max-width: 380px) {
            width:200px;
            height: 300px;
        }
    }
}

